/* Global Styles */
:root {
    --primary-color: #17a2b8;
    --dark-color: #343a40;
    --light-color: #f4f4f4;
    --danger-color: #dc3545;
    --success-color: #28a745;
}

* {
    margin: 0;
    padding: 0;
    box-sizing: border-box;
}

body {
    font-family: "Raleway", sans-serif;
    font-size: 1rem;
    line-height: 1.6;
    background-color: #fff;
    color: #333;
}

a {
    text-decoration: none;
    color: var(--primary-color);
}

.navbar {
    display: flex;
    justify-content: space-between;
    position: fixed;
    width: 100%;
    z-index: 1;
    top: 0;
    opacity: 0.9;
    background-color: skyblue;
    align-items: center;
    padding: 0.5rem 3rem 0.5rem 4rem;
    height: 4rem;
}

.nav-title {
    font-weight: 590;
    font-size: 2rem;
}

.nav-title a {
    text-decoration: none;
    color: #000;
}

.nav-links {
    display: grid;
    grid-template-columns: repeat(3, auto);
}

.nav-link {
    list-style-type: none;
}

.nav-link a {
    text-decoration: none;
    padding: 0.5rem 2rem;
    font-size: 1.2rem;
    color: #000;
}

.nav-link a:hover {
    opacity: 0.7;
    border-radius: 10px;
    transition: 0.7s ease-out;
}

.landing {
    position: relative;
}

.landing-inner {
    position: absolute;
    transform: translate(30%, 12rem);
}

.landing-title {
    font-size: 4rem;
}

.buttons {
    text-align: center;
}

.btn {
    display: inline-block;
    background: var(--light-color);
    color: #333;
    padding: 0.4rem 1.3rem;
    font-size: 1rem;
    border: none;
    cursor: pointer;
    margin-right: 0.5rem;
    transition: opacity 0.2s ease-in;
    outline: none;
    text-decoration: none;
}

.btn:hover {
    opacity: 0.8;
}

.btn-primary,
.bg-primary {
    background-color: var(--primary-color);
    color: #fff;
}

.btn-light,
.bg-light {
    background-color: var(--light-color);
    color: #333;
}

.btn-dark,
.bg-dark,
.badge-dark,
.alert-dark {
    background: var(--dark-color);
    color: #fff;
}

.btn-danger {
    background-color: var(--danger-color);
    color: #fff;
}

.container {
    max-width: 1100px;
    margin: auto;
    overflow: hidden;
    padding: 0 2rem;
    margin-top: 6rem;
    margin-bottom: 3rem;
}

.alert {
    padding: 0.8rem;
    margin: 1rem 0;
    opacity: 0.9;
    background: var(--light-color);
    color: #333;
}

.alert-danger {
    background-color: var(--danger-color);
    color: #fff;
}

.alert-success,
.btn-success {
    background-color: var(--success-color);
    color: #fff;
}

/* Margin */
.m {
    margin: 0.5rem;
}
.m-1 {
    margin: 1rem;
}
.m-2 {
    margin: 2rem;
}
.m-3 {
    margin: 3rem;
}
.my {
    margin: 0.5rem 0;
}
.my-1 {
    margin: 1rem 0;
}
.my-2 {
    margin: 2rem 0;
}
.my-3 {
    margin: 3rem 0;
}

.lead {
    font-size: 1.5rem;
    margin-bottom: 1rem;
}

/* Forms */
.form .form-group {
    margin: 1.2rem 0;
}

.form .form-text {
    display: block;
    margin-top: 0.3rem;
    color: #888;
}

.form input[type="text"],
.form input[type="email"],
.form input[type="password"],
.form input[type="date"],
.form select,
.form textarea {
    display: block;
    width: 100%;
    padding: 0.4rem;
    font-size: 1.2rem;
    border: 1px solid #ccc;
}

.form input[type="submit"],
button {
    font: inherit;
}

.dash-inner {
    text-align: center;
}

.dash-title {
    font-size: 3rem;
}

/* Text Styles*/
.x-large {
    font-size: 4rem;
    line-height: 1.2;
    margin-bottom: 1rem;
}

.large {
    font-size: 3rem;
    line-height: 1.2;
    margin-bottom: 1rem;
}

.lead {
    font-size: 1.5rem;
    margin-bottom: 1rem;
}

.text-center {
    text-align: center;
}

.text-primary {
    color: var(--primary-color);
}

.text-dark {
    color: var(--dark-color);
}

.form input[type="submit"],
button {
    font: inherit;
}

.form .social-input {
    display: flex;
}

.form .social-input i {
    padding: 0.5rem;
    width: 4rem;
}

.form .social-input i.fa-twitter {
    color: #38a1f3;
}
.form .social-input i.fa-facebook {
    color: #3b5998;
}
.form .social-input i.fa-instagram {
